.button {
  width: 130px;
  height: 30px;
  padding: 5px 24px;
  border-radius: 3px;
  border: none;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  text-align: center;
  margin-right: 25px;
  background-color: #0066CC;
  color: white;

}


.disabled {
  width: 133px;
  height: 30px;
  padding: 5px 24px;
  border-radius: 3px;
  opacity: 0.5;
  background: #004D99;
}

.secondary {
  /* width: 176px; */
  width: 130px;
  height: 30px;
  padding: 5px 24px;
  border-radius: 3px;
  border: 1px solid #0066CC;
  background-color: white;
  color: #0066CC;
  font-family: Montserrat;

}

.default {
  width: 132px;
  height: 30px;
  padding: 5px 24px;
  border-radius: 3px;
  background: #0066CC;
  border: none;
  color: white;
}

.red {
  width: 129px;
  height: 30px;
  padding: 5px 24px;
  border-radius: 3px;
  background: #CC1439;
  border: none;
  color: white;
}

.default:hover {
  background: #0080FF;
}

.red:hover {
  background: #FF1A47;
}

.secondary:hover {
  background: #E5F0FA;
  border: 1px solid #0066CC;
  color: #0066CC;
}

.default:active {
  background: #0059B2;
}

.secondary:active {
  background: #C9E5FF;
}

.red:active {
  background: #B21232;
}

.button:disabled {
  background: #004D99;
}